<template>
    <div class="content trends">
        <div  class="gifts-list" style="width:100%">
        <gift-card v-for="(offer, index) in filterOffers" :key="index"
            :name="offer.name"
            :replacers="offer.replacers"
            :img="offer.image_path"
            ></gift-card>
        </div>

    </div>
</template>
<script>
import GiftCard from '../Basics/GiftsCard.vue'
import axios from 'axios';
export default{
    props:['offers'],
    components:{GiftCard},
    data(){
        return{
            selectedOffers: [],
            load: true
        }
    },
    computed:{
        filterOffers(){
            // console.log(this.selectedOffers);
            return this.selectedOffers.filter(element => {
                return element.length != 0
            })
        },
    },
    methods:{
        searchParam(param) {
            const urlSearch = new URLSearchParams(window.location.search)
            let value = urlSearch.get(param);
            return value ? value : null;
        },
    },
    async mounted(){
        this.load = true;

        let selectedId = this.$store.getters['selectedId'];
        
        if(selectedId){
            let headers = {
                'Content-Type': 'application/json',
                'CLIENT-TYPE': 'web'
            }
            let api =   'https://api.kayanchat.live/v1'
            let userKey =  this.searchParam('sk');
            await axios.get(api+'/games/decoration-offers/archive',{
                params:{
                    sk: userKey,
                    offer_id: selectedId
                },
                headers:headers
            }).then(response => {
                this.selectedOffers = response.data.data;
            }).catch(err => {
                this.err = err.message;
            })
        }else{
            this.selectedOffers = this.offers;
        }
        this.load = false
    }
}
</script>
<style>
.trend-gifts{
    margin-top: 35px;
    width: 100% !important;
}
</style>