<template>
    <div class="content gifts">
        <v-overlay @click="alert = false" :value="alert && msg.error != null" class="d-flex justify-center overlay">
            <div v-show="alert">
                <v-card class="white rounded custom-card">
                    <v-card-title class="d-flex justify-center" :class="msg.error ? 'red darken-2' : 'teal lighten-1'">
                        <v-icon x-large>{{msg.error ? 'mdi-close-circle' : 'mdi-check-circle'}}</v-icon>
                    </v-card-title>
                    <v-card-text class="mt-3 alert-text font-weight-normal">{{msg.text}}</v-card-text>
                </v-card>
            </div>
        </v-overlay>


        <div v-show="$store.state.selectedId == offer.id" class="gifts-list"  v-for="(offer , index) in trends" :key="'g-i-k'+index">

            <card :offers="offer"
                @purchase="payNow"
                />

        </div>



        <div class="days-list">
            <div class="day" v-for="(day, index) in days" :key="index">
                <button class="btn-day" :class="day.id == $store.getters['selectedId'] ? 'btn-active' : ''" @click="getTrends(day.date,day.id)" >{{day.name}}</button>
            </div>
        </div>
    </div>
</template>
<script>
import Card from './Card.vue'
import axios from 'axios';

export default{
    props:['trends','userDiamons'],
    components:{Card},
    data(){
        return{
            days: [],
            currentTrend: null,
            alert: false,
            msg:{
                error: null,
                text: ''
            }
        }
    },
    methods:{
        getTrends(data,id){
            // this.$store.dispatch('selectedId', )
            // console.log(data);
            this.$store.dispatch('setSelectedId',id);
            
            this.currentTrend = this.trends.find(element => {
                return element.ends_at == data
            }).decorations
            
        },
        payNow(itemId,diamons){
            
            if(diamons > this.userDiamons){
                this.alert = true;
                this.msg.error = true;
                this.msg.text = 'حدث خطاء اثناء الشراء'
            }else{
                let selectedOffer = this.$store.getters['selectedId'];
                axios.defaults.headers['content-type'] = 'application/json';
                axios.defaults.headers['CLIENT-TYPE'] = 'web';
                
                let api =   'https://api.kayanchat.live/v1'
                let userKey =    this.searchParam('sk');
                axios.post(api+`/games/decoration-offers/purchase/${selectedOffer}/${itemId}?sk=${userKey}`).then((response) => {
                    this.alert = true;
                    this.msg.error = false;
                    this.msg.text = response.data.meta.message;
                }).catch((response) => {
                    this.alert = true;
                    this.msg.error = Boolean(!response.response.data.success);
                    this.msg.text = response.response.data.error.message;
                })
            }
            
        },
        searchParam(param) {
            const urlSearch = new URLSearchParams(window.location.search)
            let value = urlSearch.get(param);
            return value ? value : null;
        },
    },
    mounted(){
        let trends = this.trends;
        trends.forEach(element => {
            let dayObj = {
                name: element.ends_at.substr(5,5).replace('-','/'),
                date: element.ends_at,
                id: element.id
            
            }
            this.days.push(dayObj);
        })
        
        this.currentTrend = this.trends[0].decorations;
        if(this.$store.getters['selectedId']){
            this.currentTrend = this.trends.find(element => {
                return element.id == this.$store.getters['selectedId']
            }).decorations
        }
    }
}
</script>
<style>
.content.gifts{
    animation-duration: 0.5s;
    animation-name: slidein;
}
.overlay .v-overlay__content{
    /* width: 0px; */
    /* width: 0%; */
    width: auto !important;
    font-family: 'Cairo', sans-serif;
    
}
.alert-text{
    font-size: 20px !important;
    color: #37474F !important;
}
.custom-card{
    animation-name: sizing;
    animation-duration: 0.5s;
}
.active{
    background-color: #009864;
}

.btn-day{
    width: 100%;
}
.btn-active{
    padding: 10px 0px;
    background-color: #96230a;
}
@keyframes slidein {
  from {
    position: relative;
    right: -100%;
  }

  to {
    position: relative;
    right: 0%;
  }
}
@keyframes sizing{
    from{
        transform: scale(0.5);
    }
    to{
        transform: scale(1);
    }
}

</style>