<template>
    <div class="toggle-buttons">
        <button @click="setComponent('trend-gifts')" :class="`${active == 'trend-gifts' ? 'active-button' : 'toggle-button'}`" class="btn">المتصدرين</button>
        <button @click="setComponent('gifts')" :class="`${active == 'gifts' ? 'active-button' : 'toggle-button'}`" class="btn">الهدايا</button>
    </div>
</template>
<script>
export default{
    emits:['component'],
    data(){
        return{
            active: 'gifts'
        }
    },
    methods:{
        setComponent(page){
            this.$emit('setpage', page);
            this.active = page;
        }
    }
}
</script>