import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
    state(){
        return{
            selectedId: null
        }
    },
    getters:{
        selectedId(state){
            return state.selectedId
        }
    },
    mutations:{
        setSelectedId(state,data){
            state.selectedId = data;
        }
    },
    actions:{
        setSelectedId(context,data){
            context.commit('setSelectedId', data);
        }
    }
})
