<template>
    <div>
        <div :class="{active: offers.available_now}"  class="gifts card mb-2" v-for="(decoration , index) in offers.decorations" :key="index" >
            <div class="card-info w-100">
                <div class="card-content">
                    <h3 class="text-right card-title">{{decoration.name}}</h3>
                    <div class="pay-limits d-flex align-center justify-space-between mb-2">
                        <div class="golds d-flex align-center">
                            <img width="20px" class="mr-2" src="../../assets/gold.png">
                            <span class="gold-price">{{decoration.currency_count}}</span>
                        </div>
                        <span class="limit-text">الشراء محدود {{decoration.max_purchases}} فقط</span>
                    </div>
                    <div class="card-actions">
                        <span class="action-text">تم الشراء من {{decoration.current_purchases}}</span>
                        <button :disabled="!offers.available_now" @click="payNow(decoration.id,decoration.currency_count)" class="action-btn">
                            شراء
                        </button>
                    </div>
                </div>
            </div>
            <div class="card-pic">
                <img :src="decoration.image_path" alt="Price">
            </div>
    </div>
    </div>
</template>
<script>
export default{
    emits:['purchase'],
    // props:['name','coins','limits','id','img','max','current'],
    props:['offers'],
    methods:{
        payNow(id,coins){
            this.$emit('purchase',id,coins)
        }
    }
}
</script>
<style>
.card{
    background-color: #FFF;
    display: flex;
    overflow: hidden;
    border-radius: 10px;
    /* border: 3px solid #01764e; */

    justify-content: space-between;
}
.gifts.card{
    opacity: 0.5;
}
.gifts.card.active{
    opacity: 1;
}
.card-pic{
    background-color: #01764e;
    border: 1px solid #01764e;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    width: 75px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.card-pic img {
    width: 75%;
    object-fit: contain;
}
.card-info{
    border-radius: 10px;
    padding: 8px;
    border: 2px 0px 2px 2px solid #01764e;
    font-family: 'Cairo', sans-serif;
    width: calc(100% - 75px);
}


</style>