<template>
    <div class="container">
        <toggle-buttons @setpage="setComponent"></toggle-buttons>
        <component :is="component" :userDiamons="diamonds" :trends="trends" :offers="offers"></component>
    </div>
</template>
<script>
import Gifts from './Basics/MainGifts.vue'
import ToggleButtons from '../components/Basics/Buttons.vue'
import Trends from './Basics/MainTrends.vue'
import TrendGifts from '../components/Basics/TrendGifts.vue'
export default{
    props:['offers','trends','diamonds'],
    components:{ToggleButtons,Gifts,Trends,TrendGifts},
    data(){
        return{
            component: 'gifts'
        }
    },
    methods:{

        setComponent(component){
            this.component = component;
        }
    }
    
}
</script>