<template>
    <div class="main">
        <v-overlay class="rules" @click="showRules = false"  :value="showRules">
            <div class="container">
                <div class="img-container">
                    <img class="rules-img" src="../assets/61465801-know-the-rules-.webp">
                </div>
            </div>
        </v-overlay>
        <div class="header">
            <div class="rule-button" @click="toggleOverlay">
                القواعد
            </div>
        </div>
        <div class="loading d-flex justify-center mt-5" v-if="load">
            <v-progress-circular
            :size="50"
            color="amber"
            indeterminate
            ></v-progress-circular>
        </div>
        <div class="wrap-load" v-else>
            <div class="d-flex justify-center mt-5" v-if="err">
                <span class="white--text">{{err}}</span>
            </div>
            <the-content v-if="trends && offers" :diamonds="user.wallet.diamonds" :trends="trends" :offers="offers" ></the-content>
            <trend-user v-if="user" :info="user"></trend-user>
        </div>
    </div>
</template>
<script>
import TheHeader from '../components/TheHeader.vue'
import TheContent from '../components/TheContent.vue'
import TrendUser from '../components/TrendUser.vue'
import axios from 'axios';
export default{
    components:{TheHeader, TheContent,TrendUser},
    data(){
        return{
            showRules: false,
            failedKey: null,
            user: null,
            trends:null,
            offers:null,
            load: null,
            err: null,
        }
    },
    methods:{
        toggleOverlay(data){
            this.showRules = data;
        },
        searchParam(param) {
            const urlSearch = new URLSearchParams(window.location.search)
            let value = urlSearch.get(param);
            return value ? value : null;
        },
    },
    computed:{
        selectedId(){
            return this.$store.getters['selectedId'];
        }
    },
    
    async mounted(){
        this.load = true;
        
        axios.defaults.withCredentials = false;
        // console.log(axios);

        let api =   'https://api.kayanchat.live/v1'
        let userKey =    this.searchParam('sk');

        let headers = {
            'CLIENT-TYPE': 'web'
        }
        await axios.get(api+'/games/decoration-offers/info',{
            params:{
                sk: userKey
            },
            headers:headers
        }).then(response => {
            this.user = response.data.user;
            let offers = response.data.offers;
            this.trends = offers;
            let selectedOffer = offers.find(element => {
                return element.available_now == true
            });
            this.$store.dispatch('setSelectedId', selectedOffer.id)
        }).catch(err => {
            this.err = err.message;
        })


        
        await axios.get(api+'/games/decoration-offers/archive',{
            params:{
                sk: userKey,
                offer_id: this.$store.getters['selectedId'],
            },
            headers:headers
        }).then(response => {
            this.offers = response.data.data;
        }).catch(err => {
            this.err = err.message;
        })
        this.load = false;
    }
}
</script>
<style>
.v-overlay__content{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center ;
}
.closeWindow{
    text-align: right;
    padding: 10px;
}
.img-container{
    display: flex;
    justify-content: center;
}
.rules-img{
    max-width: 90%;
    max-height: 90%;
    animation-duration: 0.5s;
    animation-name: imgLoad;

}

@keyframes imgLoad{
  from {
    /* margin-left: 100%; */
    width: 0%
  }

  to {
    width: 100%;
  }
}
</style>