<template>
    <div class="header">
        <button class="rule-button" @click="toggleOverlay">
            القواعد
        </button>
    </div>
</template>
<script>
export default{
    props:['setRules'],
    emits:['rules'],
    data(){
        return{
            overlay: setRules
        }
    },
    methods:{
        toggleOverlay(){
            this.overlay = !this.overlay;
            this.$emit('rules',this.overlay);
        }
    }
}
</script>